                    </div>
                    <div class="level">
                        <p>隐患</p>
                        <p>0</p>
                    </div>
                    <div class="level">
                        <p>通知</p>
                        <p>0</p>
                    </div>
                </div>
            </div>
            <div class="econtent">
                <div class="emessage" v-for="item in eventsList" :key="item.id">
                    <div class="eloop"><img :src="item.src" alt="">{{item.eloop}}</div>
                    <div class="ename">{{item.ename}}</div>
                    <div class="etime">
                        {{item.etime}}
                        <img src="@/assets/img/position1.png" alt="" @click="getCurrent(item)" style="cursor: pointer;">
                    </div>
                </div>
                <div class="emessage" v-show="eventsList.length == 0">
                    <div class="noneEvent" v-show="eventsList.length == 0">暂无报警</div>
                </div>
                <div class="emessage" v-show="eventsList.length == 0">
                    
                </div>
                <div class="emessage" v-show="eventsList.length == 0">
                    
                </div>
                <div class="emessage" v-show="eventsList.length == 0">
                    
                </div>
            </div>
            <div class="bottom"></div>
        </div>
    </div>
