import * as echarts from 'echarts';
import { getTodayYesTime } from '../../utils/common'
const Title = () => import('../../components/title/Title')
export default {
    data(){
        return {
            eventsList:[
                // {
                //     id:1,
                //     src:require('@/assets/img/r.png'),
                //     eloop:'智能控制实验室-1#变压器',
                //     ename:'高温',
                //     etime:'12小时前',
                //     area:'智能控制实验室',
                //     level:3
                // },
                // {
                //     id:2,
                //     src:require('@/assets/img/y.png'),
                //     eloop:'智慧配电房-2AA1-微机综保',
                //     ename:'过电压跳闸',
                //     etime:'1天前',
                //     area:'智慧配电房',
