            ],
            option:{
                animation:false,
                color:['#FF5555', '#F5AA00', '#0D94FF'],
                title: {
                    text: '\n0',
                    textStyle:{
                        fontSize:"20",
                        fontFamily:'zcoolqingkehuangyouti',
                        fontWeight:'400',
                        color:'#FFF'
                    },
                    left: 'center',
                    top:"center"
                },
                series: [
                    {
                        name: 'Access From',
                        type: 'pie',
                        center: ['50%', '50%'], // 饼图的圆心坐标
                        radius: ['75%', '85%'],
                        avoidLabelOverlap: true,
                        hoverAnimation: false,
                        label: {
                            normal: { // normal 是图形在默认状态下的样式
                                show: true,
                                position: 'center',
