        Title
    },
    mounted(){
        this.echartsInit()
        // this.alarmEvents()
    },
    methods:{
        echartsInit(){
            let chartDom = document.getElementById('myChart');
            let myChart = echarts.init(chartDom);
            myChart.setOption(this.option)

             //窗口缩放后重新调整图标尺寸
            window.onresize = function (){
                myChart.resize();
            }
        },
        getCurrent(value) {
            this.$emit('showAlarmPosition', value)
        },
        async alarmEvents(){
            let time = getTodayYesTime()
            console.log(time)
            let data ={
                start:time.today.replace(/-/g,'/'),
                end:time.today.replace(/-/g,'/'),
                projectId:this.$store.state.projectId
            }
            let res = await this.$http.alarmEvent(data)
            console.log(res);
        }
    }
}
