<style lang="less" scoped>
    .electricity{
        height: 33.3vh;
        
        width: 20vw;
        position: fixed;
        top: 68.1vh;
        right: 1vw;
        
        .echarts{
            position: relative;
            height: 25vh;
            padding-bottom: 1vh;
            background:  linear-gradient(0deg, rgba(0, 33, 59, 0.6) 0%, rgba(3, 13, 24, 0.7) 100%);

