                    width: 7.8vw;
                    height: 100%;
                    position: relative;
                    #myChart{
                        width: 11vh;
                        height: 11vh;
                        position: absolute;
                        left: 50%;
                        top: 50%;
                        transform: translate(-50% ,-50%);
                    }
                }
                .edata{
