                            color: #E6F3FD;
                            opacity: 0.7;
                            font-size: 14rem;
                            padding-bottom: 5rem;
                        }
                        p:nth-child(2){
                            padding-top: 5rem;
                            font-size: 20rem;
                            font-family: zcoolqingkehuangyouti;
                            font-weight: 400;
                            color: #FF5555;
                        }
                    }
                    .level:nth-child(2){
                        p:nth-child(2){
                            color: #F5AA00;
                        }
                    }
                    .level:nth-child(3){
                        p:nth-child(2){
                            color: #0D94FF;
                        }
                    }
                }

                .edata::before{
                    content: '';
                    width: 100%;
                    height: 1px;
                    background-color: rgba(255, 255, 255, 0.3);
                    position: absolute;
                    left: 0;
                    top: 6vh;
                }
                .edata::after{
                    content: '';
                    width: 1rem;
                    height: 1rem;
                    border: 1rem solid rgba(255, 255, 255, 0.3);
                    border-radius: 50%;
                    background-color: rgba(255, 255, 255, 0.3);
                    position: absolute;
                    left: -2rem;
                    top:calc(6vh - 1rem) ;
                }
            }

            .econtent{
                height:12vh ;
                .emessage{
                    height: 3vh;
                    line-height: 2.95vh;
                    display: flex;
                    flex-direction: row;
                    
                    .eloop{
                        padding:0 7rem  0 11rem;
                        color: rgba(255, 255, 255, 0.7);
                        font-size: 14rem;
                        font-weight: 400;
                        font-family: Source Han Sans CN;
                        width: 11vw;
                        overflow: hidden;
                        text-overflow: ellipsis;
                        white-space: nowrap;

