                        font-family: Source Han Sans CN;
                        font-weight: 400;
                        color: rgba(255, 255, 255, 0.5);
                        text-align: right;
                        img{
                            margin-left: 7rem;
                            width: 14rem;
                            height: 16rem;
                            vertical-align: -3rem;
                        }
                    }

                    .noneEvent{
                        width: 100%;
                        font-size: 16rem ;
                        text-align: center;
                        color: #E6F3FD;
                    }
                }
                .emessage:nth-child(odd){
                    background-color: rgba(164,209,255, 0.2);
                }
                
            }

            .bottom{
                width: 20vw;
                height: 1vh;
                background: url(../../assets/img/板块装饰线.png) no-repeat;
                block-size: 100% 100%;
                position: absolute;
